.wrapper {
    background-color: #1a1c28;
    padding: 50px 0;
}

.container {
    max-width: 1440px;
    padding: 0 50px;
    margin: 0 auto;
}

.content {
    display: grid;
    grid-gap: 0 100px;
    grid-template-columns: 1fr 1fr;
    align-items: center;
}

.left {
    line-height: 2;
    color: #fff;
    font-size: 18px;
    font-weight: 300;
    padding: 0 50px 0 0;

    p {
        margin-bottom: 20px;

        strong {
            color: transparent;
            font-weight: 600;
            word-break: break-word;
            background-image: linear-gradient(
                to right bottom,
                rgb(32, 227, 178),
                rgb(0, 174, 253)
            );
            background-clip: -webkit-text;
            background-clip: text;
            -webkit-background-clip: -webkit-text;
            -webkit-background-clip: text;
        }
    }

    &Link {
        display: inline-block;
        margin-top: 20px;
        padding: 2px;
        background-image: linear-gradient(to right bottom, #00aefd, #20e3b2);
        text-decoration: none;
        position: relative;

        span {
            display: block;
            padding: 10px 30px;
            background-color: #1a1c28;
            color: #fff;
        }
    }
}

.right {
    position: relative;
    border-radius: 10px;

    img {
        width: 100%;
        height: 100%;
        border-radius: inherit;
        object-fit: cover;
    }
}

@media (max-width: 1024px) {
    .wrapper {
        h3 {
            font-size: 25px;
        }
    }

    .container {
        padding: 0 20px;
    }

    .content {
        display: flex;
        flex-direction: column-reverse;
    }

    .left {
        padding: 30px 0 0 0;
    }
}
